// 1. 함수형 컴포넌트 (rsc)
import React from 'react';

// 부모 컴포넌트에 사용할 값 꽂아주기 name이랑 age같은거.
const MyName = ({name, age}) => {
    return (
        <div>
            안녕 안녕<br />
            나는 {name} ㅋㅋ<br />
            내 나이는 {age} ㅠㅠ
        </div>
    );
};

MyName.defaultProps = {
        name: "조서빈",
        age: 23
}

export default MyName;



